.title {
  font-family: "Carter One";
}
.container {
  margin-top: 50px;
  text-align: center;
}

/* Optional style for fetched images */
.images {
  display: -webkit-flex;
  display: flex;
  max-width: 80%;
  margin: 2% auto;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.single-photo {
  max-width: 300px;
  -webkit-align-self: auto;
          align-self: auto;
  margin: 1% 2px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  vertical-align: middle;
}

a img:hover {
  border: solid white 1px;
  border-radius: 10px;
}
